s: 8px;\n  padding: 16px 32px;\n  border: none;\n  cursor: pointer;\n  font-size: 16px;\n  font-weight: 600;\n  color: var(--color-primary-light);\n  font-family: \"Poppins\", sans-serif;\n}\n.button:hover {\n  background: transparent;\n}\n\n.card {\n  border-radius: 8px;\n  box-shadow: 0 8px 16px 0 rgba(24, 24, 23, 0.08);\n  background: #fff;\n  overflow: hidden;\n  cursor: pointer;\n  width: 100%;\n}\n.card__container {\n  padding: 15px;\n}\n.card__title {\n  font-size: 16px;\n  font-weight: 400;\n  text-align: center;\n}\n.card__buttons {\n  display: flex;\n  gap: 5px;\n  justify-content: center;\n}\n.card__button-detail {\n  background-color: var(--color-brand-normal);\n  border: 2px solid var(--color-brand-normal);\n}\n.card__button-detail:hover {\n  color: var(--color-brand-dark);\n}\n.card__button-buy {\n  background-color: var(--color-brand-yellow-light);\n  border: 2px solid var(--color-brand-yellow-light);\n}\n.card__button-buy:hover {\n  color: var(--color-brand-yellow-dark);\n}\n\n.input {\n  position: relative;\n}\n.input__label {\n  position: absolute;\n  top: 16px;\n  left: 42px;\n}\n.input__control {\n  padding: 8px 24px;\n  width: 100%;\n  font-size: 16px;\n  line-height: 1.17;\n  border-radius: 8px;\n  border: 1px solid var(--color-secondary-dark);\n  background-color: transparent;\n}\n.input__control:focus {\n  border-color: var(--color-brand-normal);\n  outline: none;\n}\n.input__control::placeholder {\n  color: var(--color-secondary-dark);\n  font-size: 16px;\n  line-height: 1.17;\n}\n.input__control:focus::placeholder {\n  color: var(--color-primary-dark);\n}\n@media screen and (min-width: 768px) {\n  .input__control {\n    padding: 16px 42px;\n  }\n}\n\n.link {\n  color: var(--color-primary-light);\n  text-decoration: none;\n  transition: color 0.3s;\n}\n.link:hover {\n  color: var(--color-brand-normal);\n}\n\n.order-card {\n  align-items: center;\n  display: flex;\n  flex-direction: column;\n  flex: 1;\n}\n.order-card__img {\n  width: 233px;\n  height: 195px;\n  background-image: url(\"../../public/images/delivery.png\");\n  background-size: contain;\n  background-repeat: no-repeat;\n  background-position: center;\n}\n.order-card__description {\n  text-align: center;\n  width: 233px;\n}\n.order-card__title {\n  font-size: 28px;\n  font-weight: 700;\n  margin-bottom: 10px;\n}\n\n.price {\n  display: flex;\n  gap: 8px;\n  justify-content: center;\n  align-items: center;\n}\n.price--left {\n  justify-content: left;\n}\n.price__new {\n  font-weight: 600;\n  font-size: 18px;\n  text-align: center;\n}\n.price__old {\n  color: var(--color-secondary-text);\n  font-weight: 400;\n  font-size: 14px;\n  text-decoration: line-through;\n  text-align: center;\n}\n\n.pagination {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 24px;\n}\n@media screen and (min-width: 1280px) {\n  .pagination {\n    display: none;\n  }\n}\n.pagination__button {\n  width: 12px;\n  height: 12px;\n  padding: 0;\n  background-color: var(--color-secondary-dark);\n  border-radius: 50%;\n  cursor: pointer;\n}\n.pagination__button--active {\n  background-color: var(--color-brand-normal);\n}\n\n.product__title {\n  font-size: 20px;\n  font-weight: 700;\n}\n\n.advantages__description {\n  display: flex;\n}\n.advantages__img-slider {\n  width: 50%;\n}\n\n.basket-modal {\n  position: relative;\n  width: 100%;\n  max-width: 560px;\n  height: 100%;\n  max-height: 880px;\n  background-color: var(--color-primary-light);\n  border-radius: 32px;\n  padding: 64px 48px;\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  overflow-y: auto;\n}\n@media screen and (min-width: 768px) {\n  .basket-modal {\n    padding: 48px;\n  }\n}\n.basket-modal__btn-close {\n  position: absolute;\n  top: 40px;\n  right: 40px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 0;\n  fill: transparent;\n  stroke: var(--color-primary-dark);\n  background-color: transparent;\n  border: none;\n  cursor: pointer;