 cursor: pointer;\n  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);\n  justify-content: center;\n  align-items: center;\n  position: relative;\n}\n.fabs__item:hover {\n  -webkit-transform: scale(1.1);\n  transform: scale(1.1);\n}\n.fabs__counter {\n  min-width: 30px;\n  width: initial;\n  height: 30px;\n  padding: 0 5px;\n  background-color: red;\n  color: #fff;\n  border-radius: 30px;\n  position: absolute;\n  right: -3px;\n  bottom: -3px;\n  text-align: center;\n  line-height: 30px;\n  box-sizing: border-box;\n}\n.fabs__icon {\n  fill: transparent;\n  stroke: var(--color-brand-yellow-light);\n}\n\n.footer {\n  background-color: var(--color-primary-dark);\n  padding: 32px 0;\n  position: relative;\n}\n.footer__container {\n  display: flex;\n  gap: 20px;\n  flex-direction: column;\n}\n@media screen and (min-width: 768px) {\n  .footer__container {\n    flex-direction: row;\n  }\n}\n.footer__nav, .footer__address {\n  display: flex;\n  flex-direction: column;\n  gap: 50px;\n  text-align: center;\n}\n@media screen and (min-width: 768px) {\n  .footer__nav, .footer__address {\n    flex-direction: row;\n    text-align: left;\n  }\n}\n@media screen and (min-width: 768px) {\n  .footer__nav {\n    flex-basis: 45%;\n  }\n}\n@media screen and (min-width: 768px) {\n  .footer__address {\n    flex-basis: 55%;\n  }\n}\n.footer__logo, .footer__menu {\n  flex-grow: 1;\n}\n.footer__menu {\n  display: flex;\n  flex-direction: column;\n  flex-grow: 1;\n  gap: 16px;\n}\n.footer__address-list, .footer__address-info {\n  flex-grow: 1;\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n}\n.footer__address-link {\n  color: var(--color-primary-light);\n  font-size: 16px;\n  font-weight: 300;\n  text-decoration: none;\n}\n.footer__address-link:hover {\n  color: var(--color-brand-normal);\n}\n.footer__socials {\n  display: flex;\n  justify-content: center;\n  gap: 24px;\n  margin-top: 16px;\n}\n@media screen and (min-width: 768px) {\n  .footer__socials {\n    justify-content: flex-start;\